.App {
  font-family: Helvetica, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', Arial, sans-serif;
  position: relative;
}

.header{
  background-color: #fff;
  height: 220px;
  padding:0 10%;
}
.top img {
  width:100px;
  padding-left: 60px;
  padding-top: 30px;
} 
.header .title{
  text-align: center;

 font-size: 40px;
 color: #333;
 padding-top: 30px;
}
.header .title .text{
 position: relative;
  
}
.header .title .emoji{
  position: absolute;
  right: -100px;
  bottom: 0;
  font-size: 30px;
}
.header .sub-title{
  text-align: center;

 font-size: 18px;
 color: #555;
 padding-top: 6px;
}

.roadmap{
  text-align: center;
}

.github-button{
  position: absolute;
  right: 10%;
  top: 30px;
}